












































































.saving-in-progress, .successfully-saved, .failed-to-save
  $indicatorSize: 16px

  &:before
    content: ""
    background-size: $indicatorSize $indicatorSize
    width: $indicatorSize
    height: $indicatorSize
    display: block
    position: absolute
    top: 10px
    right: -#{$indicatorSize + 16px}
    margin: auto
    z-index: 9


  &.small-height:before
    top: 4px

  &.saving-in-progress
    position: relative

    &:before
      background-image: url('/static/img/tail-spin.svg')

  &.successfully-saved
    position: relative

    &:before
      background-image: url('/static/img/tick.svg')

  &.failed-to-save
    position: relative

    &:before
      background-image: url('/static/img/cross.svg')
